input,
textarea {
    user-select: text;
    background: none;
}

.theme-dark {
    input {
        color-scheme: dark;
    }
    input::placeholder {
        color: inherit;
        opacity: 0.6;
    }
}

:not(.mat-form-field-label-wrapper) > label {
    width: 100%;
    font-size: 0.875rem;
    margin-bottom: 0.25rem;
    min-width: 100%;
    font-weight: bold;

    > span:first-child {
        color: hsl(var(--wa));
    }

    &.error {
        color: hsl(var(--er));
    }
}

#placeos {
    .svg-viewer__label {
        min-width: 12rem;
    }

    ::placeholder,
    .mat-form-field-label,
    .mat-mdc-select-placeholder {
        color: var(--n);
    }

    input {
        caret-color: var(--p);
    }
}
